.App {
    text-align: center;
    background-color: #ffffff;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
    color: rgb(255, 255, 255);
}

.App-title {
    color: rgb(64, 64, 64);
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    width: 100%;
    background-color: #00539f;
    min-height: 10vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: rgb(255, 255, 255);
    border-top-color: #00b8fb;
    border-top-style: solid;
    border-top-width: 0.25rem;
    border-bottom-color: #00b8fb;
    border-bottom-style: solid;
    border-bottom-width: 0.25rem;
}

button {
    border: none;
    border-radius: 30px;
    padding: 0.5rem;
    background-color: #00539f;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    color: white;
}
.UDbutton {
    margin-top: 20px;
    margin-bottom: 20px;
}
button:hover {
    background-color: #0072ce;
}

.button {
    margin-top: 1rem;
    margin-bottom: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
.tight {
    padding: 0;
    margin: 0;
}

.save-button {
    border: none;
    border-radius: 30px;
    padding: 0.5rem;
    background-color: #00539f;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    color: white;
    margin: auto;
}

.save-button:hover {
    background-color: #0072ce;
}

.add-button {
    border: none;
    border-radius: 30px;
    padding: 0.5rem;
    background-color: #00539f;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    color: white;
    margin: auto;
}

.add-button:hover {
    background-color: #0072ce;
}
.fulfilled {
    background-color: #51db51;
}

.not-fulfilled {
    background-color: rgb(222, 98, 98);
}

.modalBackground {
    position: fixed; /* Stay in place */
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.6); /* Black with opacity */
    display: flex;
    justify-content: center; /* Center the modal horizontally */
    align-items: center; /* Center the modal vertically */
    z-index: 1000; /* Sit on top */
}

.modalContent {
    background-color: #fff; /* White background */
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Adds depth */
    width: 750px; /* Fixed width, you can adjust as needed */
    height: 800px;
    max-height: 90vh; /* Maximum height */
    overflow-y: auto; /* Enable scroll if content is too long */
    z-index: 1001; /* Above the background */
}

.closeButton {
    background-color: transparent;
    border: none;
    cursor: pointer;
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 1.5em;
    color: #333; /* Dark color for visibility */
}

.HelpTitle {
    font-size: 36px;
}

.HelpStep {
    font-size: 20px;
}
